export function getGrid_data(type) {
    if (type === 'submit') {

        return [
            { id: 0, title: "Problem", value: "student - submit" },
            { id: 1, title: "Problem - Chapter", value: "" },
            { id: 2, title: "Problem - Number", value: "" },
            { id: 3, title: "Problem Title", input: "", value: " " },
            { id: 4, title: "Problem Description", input: "  ", value: "" },
            {
                id: 5, title: "My Code", input: "  ",
                value: "int a,b,sum=0;" +
                    "scanf(a,b,&a,&b);" +
                    "for(int i=a;i<=b;i++){" +
                    "sum+=sum+i;" +
                    "}" +
                    "printf(sum);"
            }];

    }
    else if (type === 'feedback_success') {
        return [
            { id: 0, title: "Problem", value: "student - feedback_success" },
            { id: 1, title: "Problem - Chapter", value: "" },
            { id: 2, title: "Problem - Number", value: "" },
            { id: 3, title: "Problem Title", input: "", value: " " },
            { id: 4, title: "Problem Description", input: "  ", value: "" },
            {
                id: 5, title: "My Code", input: "  ",
                value: "int a,b,sum=0;" +
                    "scanf(a,b,&a,&b);" +
                    "for(int i=a;i<=b;i++){" +
                    "sum+=sum+i;" +
                    "}" +
                    "printf(sum);"
            }];


    }
    //viewSubmit
    else {
        return [
            { id: 0, title: "Problem", value: "student - viewsubmit" },
            { id: 1, title: "Problem - Chapter", value: "" },
            { id: 2, title: "Problem - Number", value: "" },
            { id: 3, title: "Problem Title", input: "", value: " " },
            { id: 4, title: "Problem Description", input: "  ", value: "" },
            {
                id: 5, title: "My Code", input: "  ",
                value: "int a,b,sum=0;" +
                    "scanf(a,b,&a,&b);" +
                    "for(int i=a;i<=b;i++){" +
                    "sum+=sum+i;" +
                    "}" +
                    "printf(sum);"
            }];
    }
}

