export function getGrid_data(type) {
    if (type === 'register') {

        return [
            { id: 0, title: "Problem - Register", value: "register_modify" },
            { id: 1, title: "Problem - Chapter", value: "" },
            { id: 2, title: "Problem - Number", value: "" },
            { id: 3, title: "Problem Title", input: "", value: "" },
            { id: 4, title: "Problem Description", input: "  ", value: "" },
            { id: 5, title: "Answer Code", input: " ", value: "" }];

    }
    else if (type === 'modify') {
        return [
            { id: 0, title: "Problem - Modify", value: "register_modify" },
            { id: 1, title: "Problem - Chapter", value: "" },
            { id: 2, title: "Problem - Number", value: "" },
            { id: 3, title: "Problem Title", input: "", value: "" },
            { id: 4, title: "Problem Description", input: "  ", value: "" },
            { id: 5, title: "Answer Code", input: " ", value: "" }];
    }
    else {
        return [
            { id: 0, title: "FeedBack", value: "teacher - feedBack" },
            { id: 1, title: "Problem - Chapter", value: "" },
            { id: 2, title: "Problem - Number", value: "" },
            { id: 3, title: "Problem Title", input: "", value: " " },
            { id: 4, title: "Problem Description", input: "  ", value: "" },
            { id: 5, title: "Student - ID", input: " ", value: "apple123" },
            { id: 6, title: "Student - Name", input: " ", value: "" },
            {
                id: 7, title: "Student - Code", input: "  ",
                value: "int a,b,sum=0;" +
                    "scanf(a,b,&a,&b);" +
                    "for(int i=a;i<=b;i++){" +
                    "sum+=sum+i;" +
                    "}" +
                    "printf(sum);"
            },
            { id: 8, title: "My FeedBack", input: " ", value: "" }];
    }
}

