import React, { useState } from 'react';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import { Button, styled } from '@mui/material';

const DeleteBtn = styled(Button)({
    marginLeft: '0px',
    textAlign: 'center',
    backgroundColor: '#E0BFE6',
    borderRadius: '30px',
    color: '#8154A0',
    fontWeight: 'bold',
    '&:hover': {
        background: "#8154A0",
        color: "#FFF"
    }
});
const LoginBtn = styled(Button)({
    marginLeft: '0px',
    textAlign: 'center',
    backgroundColor: '#E0BFE6',
    borderRadius: '30px',
    color: '#8154A0',
    fontWeight: 'bold',
    '&:hover': {
        background: "#8154A0",
        color: "#FFF"
    }
});
const BasicBtn = styled(Button)({
    marginLeft: '0px',
    textAlign: 'center',
    backgroundColor: '#692498',
    borderRadius: '30px',
    color: '#fff',
    fontWeight: 'bold',
    '&:hover': {
        background: "#E0BFE6",
        color: "#FFF"
    }
});
const BasicBtn2 = styled(Button)({
    marginLeft: '0px',
    textAlign: 'center',
    backgroundColor: '#E0BFE6',
    borderRadius: '30px',
    color: '#8154A0',
    fontWeight: 'bold',
    '&:hover': {
        background: "#8154A0",
        color: "#FFF"
    }
});

function AlertDialog(props) {
    const [open, setOpen] = useState(false);
    const [textfield_state, setTextfield_state] = useState(props.textfield_state);

    const [button, setButton] = useState();
    const handleClickOpen = () => {
        if (props.alertDialog_title === '') {
            if (props.checkRegisterProblem()) {
                setButton(
                    <div>
                        <BasicBtn onClick={handleClose}></BasicBtn>
                        <BasicBtn onClick={handleClose} autoFocus></BasicBtn>
                    </div>
                )
                setTextfield_state(" ??");
            }
            else {
                setButton(
                    <div>
                        <BasicBtn onClick={handleClose}></BasicBtn>
                    </div>
                )
                setTextfield_state('   !!!   ');
            }
            setOpen(true);

        }
        else if (props.alertDialog_title === '') {
            setButton(
                <div>
                    <BasicBtn onClick={handleClose}></BasicBtn>
                    <BasicBtn onClick={handleClose} autoFocus></BasicBtn>
                </div>
            )
            setTextfield_state("  ??");
            setOpen(true);

        }
        else if (props.alertDialog_title === 'LOGIN') {

            if (!props.handleLogin()) {
                setButton(
                    <div>
                        <BasicBtn onClick={handleClose}></BasicBtn>
                    </div>
                )
                setTextfield_state('   !!!   ');
            } else {
                props.goClassroom();
            }

            setOpen(!props.handleLogin());
        }
        else if (props.alertDialog_title === 'SIGN UP') {
            if (!props.handleSignup()) {
                setButton(
                    <div>
                        <BasicBtn onClick={handleClose}></BasicBtn>
                    </div>
                )
                setTextfield_state('   !!!   ');
            } else {
                setButton(
                    <div>
                        <BasicBtn onClick={handleClose}></BasicBtn>
                    </div>
                )
                setTextfield_state('   ');

            }

            setOpen(true);
        }
    };

    const handleClose = (event) => {
        if (event.target.innerText === '') {
            props.handleRegisterClose();
        }
        else if (event.target.innerText === '') {
            props.handleRemoveClose(props.subunit);
        }
        else if (event.target.innerText === '') {
            props.goLogin();
        }
        setOpen(false);
    };

    return (
        < >
            {
                (() => {

                    if (props.alertDialog_title == 'LOGIN') {
                        return <button style={{
                            background: "white", marginBottom: "0px", border: "0px", fontFamily: 'esamanruLight', fontSize: "30px"
                            , marginLeft: "110px"
                        }} onClick={handleClickOpen} key="set">
                            {props.alertDialog_title}
                        </button>;
                    }
                    else if (props.alertDialog_title === '') {
                        return <DeleteBtn onClick={handleClickOpen} key="set">
                            {props.alertDialog_title}
                        </DeleteBtn>;
                    }
                    else if (props.alertDialog_title === '') {
                        return <BasicBtn onClick={handleClickOpen} key="set">
                            {props.alertDialog_title}
                        </BasicBtn>;
                    }
                    else if (props.alertDialog_title === '' || props.alertDialog_title === '') {
                        return <BasicBtn onClick={handleClickOpen} key="set">
                            {props.alertDialog_title}
                        </BasicBtn>;
                    }
                    else {
                        return <button>
                            {props.alertDialog_title}
                        </button>
                    }
                }
                )()
            }
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {props.alertDialog_title}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {textfield_state}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    {button}
                </DialogActions>
            </Dialog>
        </>
    );
}

export default AlertDialog;
